* {
  font-family: 'Arial', sans-serif;
  font-size: 1.2rem;
  word-wrap: break-word;
}

.green-colored {
  background-color: rgb(160, 248, 28);
}

.pink-colored {
  background-color: rgb(248, 181, 192);
}

.orange-colored {
  background-color: rgb(253, 169, 14);
}

.blue-colored {
  background-color: rgb(168, 219, 236);
}

.white-colored {
  background-color: rgb(255, 255, 255);
}

.red-colored {
  background-color: rgb(255, 0, 0);
}

.root {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  margin: 25px auto;
  max-width: 1200px;
  width: 100%;
  padding: 25px 0;
}

.main {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
}

.css-10nd86i {
  max-width: 270px;
  width: 100%;
}

.table {
  margin-top: 20px;
  text-align: center;
}

.table tr {
  border: 1px solid rgb(209, 209, 209);
}

.table td {
  border: 1px solid rgb(0, 0, 0);
  padding: 2px;
  width: 100%;
}

.table td:not(:first-of-type) {    
  max-width: 100px;
}

.legend {
  margin-top: 15px;
}

.legend tr {
  width: 100%;
  border: 1px solid rgb(214, 214, 214);
}

.legend td{
  border: 1px solid rgb(0, 0, 0);
  padding: 5px;
  font-size: 1rem;
}

.legend td:first-of-type {
  width: 20%;
}

.legend td:last-of-type {
  width: 80%;
}

@media screen and (max-width: 750px) {
  * {
    font-size: 0.8rem;
  }
}

@media screen and (max-width: 450px) {
  * {
    font-size: 0.5rem;  
  }
}
* {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

li {
  list-style-type: none;
}


